import detectEthereumProvider from '@metamask/detect-provider';
import { ethers, Contract } from 'ethers';
import LevenueApp from './contracts/LevenueApp.json';
// Alert library to show cool alerts based on errors
import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'


toast.configure();
const getBlockchain = () =>
  new Promise( async (resolve, reject) => {
    let provider = await detectEthereumProvider();
    if(provider) {
      await provider.request({ method: 'eth_requestAccounts' }).then(value=>{},
        reason=>{
          toast.error("Uh oh! We couldn't connect to Metamask. Please check that you have it and refresh the page.")
        });
      const networkId = await provider.request({ method: 'net_version' })
      if (networkId !== '56') {
        toast.error("Please connect to the Binance Smart Chain to use our dApp!")
      }
      provider = new ethers.providers.Web3Provider(provider);
      const signer = provider.getSigner();
      const levenue = new Contract(
        LevenueApp.networks[networkId].address,
        LevenueApp.abi,
        signer);
      resolve({levenue});
      return;

    }
    reject("install metamask");
  });

export default getBlockchain;


