  // Create radio buttons component to be inserted into HTML
  class RadioButtons extends React.Component {
    constructor (props) {
      super(props);
      this.state = {};
      this.onRadioBtnClick = this.onRadioBtnClick.bind(this);
    }
    onRadioBtnClick(rSelected) {
      this.setState({ rSelected })
    }

    render() {
      if (this.state.rSelected === 1){
        const initialInvest = document.getElementById('initialInvestment').innerHTML.substring(1).replace(',', '')
        const expectedReturn = usd.format(initialInvest*programNumber[1]/100)
        document.getElementById('expectedReturn').innerHTML = expectedReturn
      } 
      else if (this.state.rSelected === 2){
        const initialInvest = document.getElementById('initialInvestment').innerHTML.substring(1).replace(',', '')
        const expectedReturn = usd.format(initialInvest*programNumber[2]/100)
        document.getElementById('expectedReturn').innerHTML = expectedReturn
      } else if (this.state.rSelected === 3){
        const initialInvest = document.getElementById('initialInvestment').innerHTML.substring(1).replace(',', '')
        const expectedReturn = usd.format(initialInvest*programNumber[3]/100)
        document.getElementById('expectedReturn').innerHTML = expectedReturn
      } 
      
      return (
        <div>
          <ButtonGroup>
            <Button color="secondary" id="radio10" onClick={() => this.onRadioBtnClick(1)} active={this.state.rSelected === 1}>10</Button>
            <Button color="secondary" id="radio20" onClick={() => this.onRadioBtnClick(2)} active={this.state.rSelected === 2}>20</Button>
            <Button color="secondary" id="radio30" onClick={() => this.onRadioBtnClick(3)} active={this.state.rSelected === 3}>30</Button>
          </ButtonGroup>
        </div>
      );
    }
  }
